.what {
	@media (max-width : 480px) {
		padding: 20px 0; }

	&__wrap {
		display: flex;
		align-items: flex-start;
		flex-direction: column; }

	&__text {
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-bottom: 25px;
		color: $white;
		animation: fadeIn 1.5s;
		@media (max-width : 480px) {
			flex-direction: column;
			align-items: flex-start; } }

	&__icon {
		margin-right: 40px;
		width: 50px;
		height: auto;
		max-height: 80px;
		fill: $broom;
		@media (max-width : 480px) {
			margin-right: 0;
			margin-bottom: 10px;
			width: 40px;
			max-height: 50px; } }

	&__inner {
		width: 100%; }

	&__title {
		margin: 0;
		margin-bottom: 15px;
		@include adaptive-font(24, 22);
		text-align: start;
		animation: fadeIn 1s; }

	&__descr {
		margin: 0;
		@include adaptive-font(20, 18);
		opacity: .85;
		text-align: start; } }
