.header {
	width: 100%;
	min-height: 70px;
	transition: .5s;

	&.fixed {
		z-index: 10;
		position: fixed;
		top: 0;
		left: 0;
		background-color: $accent-color; }

	&__container {
		max-width: 1430px;
		margin: 0px auto;
		padding: 15px 15px;
		display: flex;
		align-items: center;
		justify-content: space-between;
		min-height: 70px;
		width: 100%;
		animation: fadeInDown 1s;
		transition: .5s;
		&.fixed-active {
			padding: 5px 15px; } }

	&--scroll {
		box-shadow: 0px 10px 7px 0px rgba(16, 16, 16, 0.35);
		background-color: #282828 !important; } }

.menu {

	&__burger {
		display: none; }

	&__item {
		margin-right: 20px;
		@media (max-width: 1100px) {
			margin-right: 15px; }

		&:last-child {
			margin: 0; } }

	&__link {
		position: relative;
		color: $broom;
		@include adaptive-font(18, 16);
		transition: color .2s;
		transition: background-color .2s;
		transition: font-size .2s;

		&::after {
			position: absolute;
			content: "";
			bottom: -3px;
			left: 50%;
			transform: translateX(-50%);
			width: 0;
			height: 2px;
			background-color: currentColor;
			opacity: 0;
			visibility: hidden;
			transition: width .5s; }
		&:focus {
			outline: 2px solid $broom;
			outline-offset: 2px; }

		&--black {
			color: $black;
			transition: padding .2s;
			&:hover {
				&::after {
					display: none; } } }

		&--scroll {
			font-size: 16px; } }

	&__link {
		&:hover {
			color: $white;

			&::after {
				width: 100%;
				color: $white;
				opacity: 1;
				visibility: visible; } } } }

@media (min-width: 992px) {

	.menu__list {
		display: flex;
		align-items: center; }

	.menu__item {
		padding: 10px 0; } }

@media (max-width: 992px) {

	.menu__burger {
		z-index: 15;
		display: block;
		position: relative;
		width: 30px;
		height: 18px;
		cursor: pointer; }

	.burger {

		&__line,

		&::before,

		&::after {
			left: 0;
			position: absolute;
			height: 10%;
			width: 100%;
			transition: all 0.3s ease 0s;
			background-color: #fff; }

		&::before,

		&::after {
			content: ""; }

		&::before {
			top: 0; }

		&::after {
			bottom: 0; }

		& span {
			top: 50%;
			transform: scale(1) translate(0px, -50%); }

		&.active span {
			transform: scale(0) translate(0px, -50%); }

		&.active::before {
			top: 50%;
			transform: rotate(-45deg) translate(0px, -50%); }

		&.active::after {
			bottom: 50%;
			transform: rotate(45deg) translate(0px, 50%); } }

	.menu__body {
		z-index: 10;
		position: fixed;
		top: 0;
		left: -100%;
		width: 100%;
		height: 100%;
		background-color: rgba(0, 0, 0, 0.9);
		padding: 100px 30px 30px 30px;
		transition: left 0.3s ease 0s;
		overflow: auto;

		&.active {
			left: 0; } }

	.menu__item {
		flex-wrap: wrap;
		margin-bottom: 30px;

		&:last-child {
			margin-bottom: 0; } }

	.menu__link {
		font-size: 20px; } }
