.how {

	&-box {
		position: relative;
		min-height: 200px;
		display: flex;
		justify-content: space-between;
		animation: fadeInUp 1s;
		@media (max-width: 810px) {
			flex-direction: column; }

		&::after {
			position: absolute;
			content: "";
			top: 50%;
			transform: translateY(-50%);
			right: 0;
			left: 0;
			width: 100%;
			height: 2px;
			background-color: $broom;
			@media (max-width: 810px) {
				display: none; } } }

	&__wrap {
		position: relative;
		display: flex;
		align-items: flex-start;
		justify-content: center;
		width: 25%;
		@media (max-width: 810px) {
			justify-content: flex-start;
			width: 100%;
			margin-bottom: 20px;
			&:last-child {
				margin: 0; } }

		&:nth-child(even) {
			align-items: end; } }

	&__text {
		margin: 0;
		max-width: 300px;
		text-align: center;
		@include adaptive-font (22, 20);
		color: $white;
		transition: all .5s;
		@media (max-width: 810px) {
			padding-left: 30px;
			text-align: left;
			max-width: 100%; }

		&::after {
			z-index: 5;
			position: absolute;
			content: "";
			width: 25px;
			height: 25px;
			border-radius: 50%;
			background-color: $broom;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			transition: all .5s;
			@media (max-width: 810px) {
				width: 17px;
				height: 17px;
				left: 5px; } }

		&:hover {
			@include adaptive-font (24, 22);
			color: $white;

			&::after {
				background-color: $white;
				width: 30px;
				height: 30px;
				@media (max-width: 810px) {
					width: 15px;
					height: 15px;
					background-color: $broom; } }

			@media (max-width: 810px) {
				@include adaptive-font (22, 20);
				color: $white; } } } }
