.form {
	@media (max-width : 480px) {
		padding: 20px 0; }

	&__wrap {
		display: flex;
		justify-content: flex-start;
		@media (max-width : 992px) {
			flex-direction: column; } }

	&-inner {
		width: 100%;
		animation: fadeInRight 1s;

		&__form-group {
			display: flex;
			align-items: center;
			justify-content: space-between;
			gap: 30px;
			width: 100%;
			@media (max-width : 480px) {
				flex-direction: column;
				gap: 0; } } }

	&-group {
		width: 100%;
		margin-bottom: 20px; }

	&-name {
		display: inline-block;
		margin-bottom: 20px;
		@include adaptive-font (18, 16);
		color: $white; }

	&-control {
		display: block;
		padding: 20px 15px;
		width: 100%;
		background-color: $accent-color;
		border: none;
		border: 2px solid $broom;
		border-radius: 5px;
		color: $white;

		&::placeholder {
			color: #ffffff75; }

		&:focus {
			color: $white;
			background-color: $black;
			border-color: $broom;
			outline: 0;
			box-shadow: 0px 0px 15px 5px rgba($broom, 0.445);
			transition: border-color 0.2s ease-in-out, box-shadow 0.2s ease-in-out; } }

	&-text {
		@extend .form-control;
		resize: none;
		width: 100%;
		min-height: 150px; } }

.btn--form {
	margin-bottom: 20px;
	&:disabled {
		opacity: .5;
		&:hover {
			border: 2px $broom solid;
			background-color: $accent-color;
			color: $white;
			cursor: inherit; } } }

.info {
	display: flex;
	flex-direction: column;
	flex-shrink: 0;
	margin-right: 100px;
	@media (max-width : 992px) {
		margin-right: 0;
		margin-bottom: 50px; }

	&__wrap {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		height: 100%;
		animation: fadeInLeft 1s; }

	&__inner {
		margin-bottom: 30px;
		&:last-child {
			margin: 0; } }

	&__descr {
		margin: 0;
		margin-bottom: 15px;
		@include adaptive-font(22, 18);
		color: $white;
		&:last-child {
			margin-bottom: 0; }

		&--accent {
			color: $broom;
			@include adaptive-font(24, 20); }

		&--sale {
			position: relative;
			&::after {
				position: absolute;
				content: "";
				width: 120%;
				height: 3px;
				background-color: #ff0000;
				bottom: 50%;
				left: -5px;
				right: 0;
				transform: rotate(170deg); } } }

	&__limit {
		margin: 0;
		margin-bottom: 5px;
		@include adaptive-font(18, 16);
		color: $white;
		font-style: italic;
		&:last-child {
			margin-bottom: 0; }

		&--accent {
			color: $broom; } } }
