.politics {
	padding-top: 100px;
	color: $white;
	line-height: 1.4;
	opacity: 0.85;

	&__container {
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column; }

	&__version {
		width: 100%;
		margin-bottom: 30px;
		font-style: italic; }

	&__title {
		max-width: 100%;
		color: $white;
		@include adaptive-font(30, 22); }

	&__subtitle {
		width: 100%;
		margin: 0;
		margin-bottom: 15px;
		@include adaptive-font(24, 18); }

	&__descr {
		margin: 0;
		margin-bottom: 30px;
		@include adaptive-font(18, 16); }

	&-list {
		padding-left: 50px;
		margin-bottom: 40px;
		@include adaptive-font(18, 16);
		@media (max-width : 720px) {
			padding-left: 20px; }

		&__item {
			margin-bottom: 5px; } }

	&__text {
		margin: 0;
		margin-bottom: 5px; }

	&__link {
		text-decoration: underline; } }
