.footer {
	border-top: 1px solid #101010;
	background-color: #282828;

	&__container {
		width: 100%;
		min-height: max-content;
		padding: 30px 15px; }

	&__wrap {
		display: flex;
		align-items: flex-end;
		justify-content: space-between;
		animation: fadeInUpBig 1s;
		@media (max-width: 992px) {
			flex-direction: column-reverse;
			align-items: center; } }

	&__logo {
		@media (max-width: 992px) {
			order: 1;
			margin-bottom: 10px; } }

	&__info {
		min-width: 260px;
		@include adaptive-font(16, 16);
		@media (max-width: 992px) {
			text-align: center; } }

	&__link {
		text-align: right;
		transition: opacity .3s;
		&:hover {
			opacity: 0.85; }
		&:focus {
			outline: 2px solid $broom;
			outline-offset: 2px; }
		@media (max-width: 992px) {
			text-align: center;
			margin-bottom: 15px; } } }
