@font-face {
  src: url("../fonts/Derold.woff2") format("woff2"), url("../fonts/Derold.woff") format("woff");
  font-family: "Derold";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  src: url("../fonts/Circe.woff2") format("woff2"), url("../fonts/Circe.woff") format("woff");
  font-family: "Circe";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
  height: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "Circe", sans-serif;
  font-weight: 400;
  color: #E7E61A;
  background-color: #222222;
  height: 100%;
}

body.lock {
  overflow: hidden;
}

.site-container {
  overflow: hidden;
  min-height: 100%;
  line-height: 1.2;
  display: flex;
  flex-direction: column;
}

.main {
  flex: 1 1 auto;
}

.container {
  margin: 0 auto;
  max-width: 1430px;
  padding: 0 15px;
}

img {
  max-width: 100%;
  object-fit: cover;
}

.link-reset {
  text-decoration: none;
  color: inherit;
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

.btn-reset {
  padding: 0;
  border: none;
  color: inherit;
  background: transparent;
  cursor: pointer;
}

.is-hidden {
  display: none;
}

.error {
  border: 2px #f06464 solid;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

