  .menu__item {
    margin-right: 15px;
  }
}

.menu__item:last-child {
  margin: 0;
}

.menu__link {
  position: relative;
  color: #E7E61A;
  font-size: calc(16px + 2 * ((100vw - 320px) / 1110));
  transition: color .2s;
  transition: background-color .2s;
  transition: font-size .2s;
}

.menu__link::after {
  position: absolute;
  content: "";
  bottom: -3px;
  left: 50%;
  transform: translateX(-50%);
  width: 0;
  height: 2px;
  background-color: currentColor;
  opacity: 0;
  visibility: hidden;
  transition: width .5s;
}

.menu__link:focus {
  outline: 2px solid #E7E61A;
  outline-offset: 2px;
}

.menu__link--black {
  color: #111111;
  transition: padding .2s;
}

.menu__link--black:hover::after {
  display: none;
}

.menu__link--scroll {
  font-size: 16px;
}

.menu__link:hover {
  color: #ffffff;
}

.menu__link:hover::after {
  width: 100%;
  color: #ffffff;
  opacity: 1;
  visibility: visible;
}

@media (min-width: 992px) {
  .menu__list {
    display: flex;
    align-items: center;
  }
  .menu__item {
    padding: 10px 0;
  }
}

@media (max-width: 992px) {
  .menu__burger {
    z-index: 15;
    display: block;
    position: relative;
    width: 30px;
    height: 18px;
    cursor: pointer;
  }
  .burger__line, .burger::before, .burger::after {
    left: 0;
    position: absolute;
    height: 10%;
    width: 100%;
    transition: all 0.3s ease 0s;
    background-color: #fff;
  }
  .burger::before, .burger::after {
    content: "";
  }
  .burger::before {
    top: 0;
  }
  .burger::after {
    bottom: 0;
  }
  .burger span {
    top: 50%;
    transform: scale(1) translate(0px, -50%);
  }
  .burger.active span {
    transform: scale(0) translate(0px, -50%);
  }
  .burger.active::before {
    top: 50%;
    transform: rotate(-45deg) translate(0px, -50%);
  }
  .burger.active::after {
    bottom: 50%;
    transform: rotate(45deg) translate(0px, 50%);
  }
  .menu__body {
    z-index: 10;
    position: fixed;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.9);
    padding: 100px 30px 30px 30px;
    transition: left 0.3s ease 0s;
    overflow: auto;
  }
  .menu__body.active {
    left: 0;
  }
  .menu__item {
    flex-wrap: wrap;
    margin-bottom: 30px;
  }
  .menu__item:last-child {
    margin-bottom: 0;
  }
  .menu__link {
    font-size: 20px;
  }
}

