.how__wrap {
  position: relative;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  width: 25%;
}

@media (max-width: 810px) {
  .how__wrap {
    justify-content: flex-start;
    width: 100%;
    margin-bottom: 20px;
  }
  .how__wrap:last-child {
    margin: 0;
  }
}

.how__wrap:nth-child(even) {
  align-items: end;
}

.how__text {
  margin: 0;
  max-width: 300px;
  text-align: center;
  font-size: calc(20px + 2 * ((100vw - 320px) / 1110));
  color: #ffffff;
  transition: all .5s;
}

@media (max-width: 810px) {
  .how__text {
    padding-left: 30px;
    text-align: left;
    max-width: 100%;
  }
}

.how__text::after {
  z-index: 5;
  position: absolute;
  content: "";
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background-color: #E7E61A;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: all .5s;
}

@media (max-width: 810px) {
  .how__text::after {
    width: 17px;
    height: 17px;
    left: 5px;
  }
}

.how__text:hover {
  font-size: calc(22px + 2 * ((100vw - 320px) / 1110));
  color: #ffffff;
}

.how__text:hover::after {
  background-color: #ffffff;
  width: 30px;
  height: 30px;
}

@media (max-width: 810px) {
  .how__text:hover::after {
    width: 15px;
    height: 15px;
    background-color: #E7E61A;
  }
}

@media (max-width: 810px) {
  .how__text:hover {
    font-size: calc(20px + 2 * ((100vw - 320px) / 1110));
    color: #ffffff;
  }
}

@media (max-width: 480px) {
  .whom {
    padding: 20px 0;
  }
}

.whom__wrap {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
}

.whom__text {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 40px;
  color: #ffffff;
  animation: fadeIn 1.5s;
}

@media (max-width: 480px) {
  .whom__text {
    flex-direction: column;
  }
}

.whom__icon {
  margin-right: 100px;
  width: 75px;
  max-height: 80px;
  height: auto;
  fill: #E7E61A;
}

@media (max-width: 768px) {
  .whom__icon {
    margin-right: 40px;
    max-height: 50px;
  }
}

@media (max-width: 480px) {
  .whom__icon {
    margin: 0 auto;
    margin-bottom: 15px;
  }
}

.whom__inner {
  position: relative;
  width: 100%;
}

.whom__inner::after {
  content: '';
  position: absolute;
  top: 50%;
  left: -55px;
  width: 10px;
  height: 10px;
  border-radius: 100%;
  background-color: #E7E61A;
  transform: translateY(-50%);
}

@media (max-width: 768px) {
  .whom__inner::after {
    display: none;
  }
}

.whom__title {
  margin: 0;
  margin-bottom: 15px;
  font-size: calc(22px + 2 * ((100vw - 320px) / 1110));
  text-align: start;
}

.whom__descr {
  margin: 0;
  font-size: calc(18px + 2 * ((100vw - 320px) / 1110));
  opacity: .85;
  text-align: start;
}

@media (max-width: 480px) {
  .modules {
    padding: 20px 0;
  }
}

.tabs {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 50px;
}

@media (max-width: 992px) {
  .tabs {
    flex-direction: column;
  }
}

.tabs__header {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  flex-direction: column;
  flex-shrink: 0;
  gap: 20px;
  margin-right: 20px;
  animation: fadeInLeftBig 1s;
}

@media (max-width: 992px) {
  .tabs__header {
    flex-direction: row;
    justify-content: center;
    margin-bottom: 40px;
  }
}

.tabs__header-item {
  cursor: pointer;
  color: #ffffff;
  border-bottom: 2px #ffffff dashed;
  padding-bottom: 2px;
  transition: all .3s;
}

.tabs__header-item:hover {
  border-bottom: 2px #E7E61A solid;
  color: #E7E61A;
}

.tabs__header-item:focus {
  outline: 2px solid #E7E61A;
  outline-offset: 2px;
}

.tabs__header-item.active {
  color: #E7E61A;
  border-bottom: 2px #E7E61A solid;
}

.tabs__content {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  animation: fadeInUpBig 1s;
}

.tabs__content-item {
  max-width: 1200px;
  display: flex;
  text-align: center;
  flex-direction: column;
  animation: 1.5s fadeInRight;
}

.tabs__content-title {
  margin: 0;
  margin-bottom: 15px;
  font-size: calc(25px + 25 * ((100vw - 320px) / 1110));
  color: #ffffff;
}

.tabs__content-text {
  margin: 0;
  font-size: calc(20px + 2 * ((100vw - 320px) / 1110));
  color: #ffffff;
}

@media (max-width: 480px) {
  .question {
    padding: 20px 0;
  }
}

.spollers {
  padding-bottom: 20px;
}

.spollers__item {
  background-color: #222222;
  border-top: 1px solid #464a50;
  animation: fadeInLeft 1s;
}

.spollers__item:last-child {
  margin-bottom: 0;
}

.spollers__title {
  width: 100%;
  text-align: left;
  font-size: calc(20px + 2 * ((100vw - 320px) / 1110));
  color: #ffffff;
  padding: 10px;
  position: relative;
}

.spollers__title:focus {
  outline: 2px solid #E7E61A;
  outline-offset: 2px;
}

.spollers.init .spollers__title {
  padding: 25px 40px 25px 10px;
}

.spollers.init .spollers__title::before,
.spollers.init .spollers__title::after {
  content: "";
  width: 20px;
  height: 2px;
  background-color: #ffffff;
  position: absolute;
  right: 10px;
  top: 33px;
  transition: transform 0.3s ease 0s;
}

.spollers.init .spollers__title::after {
  transform: rotate(-90deg);
}

.spollers.init .spollers__title.active::after {
  transform: rotate(0deg);
}

.spollers__text {
  padding: 0 30px 20px;
  color: #ffffff;
  opacity: .85;
  line-height: 130%;
  font-size: calc(18px + 2 * ((100vw - 320px) / 1110));
}

@media (max-width: 480px) {
  .form {
    padding: 20px 0;
  }
}

.form__wrap {
  display: flex;
  justify-content: flex-start;
}

@media (max-width: 992px) {
  .form__wrap {
    flex-direction: column;
  }
}

.form-inner {
  width: 100%;
  animation: fadeInRight 1s;
}

.form-inner__form-group {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 30px;
  width: 100%;
}

@media (max-width: 480px) {
  .form-inner__form-group {
    flex-direction: column;
    gap: 0;
  }
}

.form-group {
  width: 100%;
  margin-bottom: 20px;
}

.form-name {
  display: inline-block;
  margin-bottom: 20px;
  font-size: calc(16px + 2 * ((100vw - 320px) / 1110));
  color: #ffffff;
}

.form-control, .form-text {
  display: block;
  padding: 20px 15px;
  width: 100%;
  background-color: #222222;
  border: none;
  border: 2px solid #E7E61A;
  border-radius: 5px;
  color: #ffffff;
}

.form-control::placeholder, .form-text::placeholder {
  color: #ffffff75;
}

.form-control:focus, .form-text:focus {
  color: #ffffff;
  background-color: #111111;
  border-color: #E7E61A;
  outline: 0;
  box-shadow: 0px 0px 15px 5px rgba(231, 230, 26, 0.445);
  transition: border-color 0.2s ease-in-out, box-shadow 0.2s ease-in-out;
}

.form-text {
  resize: none;
  width: 100%;
  min-height: 150px;
}

.btn--form {
  margin-bottom: 20px;
}

.btn--form:disabled {
  opacity: .5;
}

.btn--form:disabled:hover {
  border: 2px #E7E61A solid;
  background-color: #222222;
  color: #ffffff;
  cursor: inherit;
}

.info {
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  margin-right: 100px;
}

@media (max-width: 992px) {
  .info {
    margin-right: 0;
    margin-bottom: 50px;
  }
}

.info__wrap {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  animation: fadeInLeft 1s;
}

.info__inner {
  margin-bottom: 30px;
}

.info__inner:last-child {
  margin: 0;
}

.info__descr {
  margin: 0;
  margin-bottom: 15px;
  font-size: calc(18px + 4 * ((100vw - 320px) / 1110));
  color: #ffffff;
}

.info__descr:last-child {
  margin-bottom: 0;
}

.info__descr--accent {
  color: #E7E61A;
  font-size: calc(20px + 4 * ((100vw - 320px) / 1110));
}

.info__descr--sale {
  position: relative;
}

.info__descr--sale::after {
  position: absolute;
  content: "";
  width: 120%;
  height: 3px;
  background-color: #ff0000;
  bottom: 50%;
  left: -5px;
  right: 0;
  transform: rotate(170deg);
}

.info__limit {
  margin: 0;
  margin-bottom: 5px;
  font-size: calc(16px + 2 * ((100vw - 320px) / 1110));
  color: #ffffff;
  font-style: italic;
}

.info__limit:last-child {
  margin-bottom: 0;
}

.info__limit--accent {
  color: #E7E61A;
}

.checkbox {
  max-width: max-content;
}

.checkbox__item {
  cursor: pointer;
  display: flex;
  align-items: center;
  margin-right: 30px;
}

.checkbox__input {
  display: none;
}

.checkbox__input:checked + .fake::before {
  opacity: 1;
}

.checkbox__input:checked + .fake {
  background-color: #222222;
  border: 2px solid #E7E61A;
}

.checkbox .fake {
  min-height: 18px;
  min-width: 18px;
  box-sizing: border-box;
  border: 2px solid #E7E61A;
  border-radius: 2px;
  display: block;
  margin-right: 8px;
  position: relative;
}

.checkbox .fake::before {
  content: '';
  position: absolute;
  display: block;
  height: 18px;
  width: 8px;
  border-bottom: 2px solid #ffffff;
  border-right: 2px solid #ffffff;
  top: -6px;
  box-sizing: border-box;
  left: 5px;
  transform: rotate(35deg);
  opacity: 0;
  transition: .3s all ease;
}

.checkbox__text {
  margin: 0;
  display: inline-block;
  color: #ffffff;
  opacity: .85;
}

.checkbox__link {
  text-decoration: underline;
}

.checkbox__link:focus {
  outline: 2px solid #E7E61A;
  outline-offset: 2px;
}

.footer {
  border-top: 1px solid #101010;
  background-color: #282828;
}

.footer__container {
  width: 100%;
  min-height: max-content;
  padding: 30px 15px;
}

.footer__wrap {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  animation: fadeInUpBig 1s;
}

@media (max-width: 992px) {
  .footer__wrap {
    flex-direction: column-reverse;
    align-items: center;
  }
}

@media (max-width: 992px) {
  .footer__logo {
    order: 1;
    margin-bottom: 10px;
  }
}

.footer__info {
  min-width: 260px;
  font-size: calc(16px + 0 * ((100vw - 320px) / 1110));
}

@media (max-width: 992px) {
  .footer__info {
    text-align: center;
  }
}

.footer__link {
  text-align: right;
  transition: opacity .3s;
}

.footer__link:hover {
  opacity: 0.85;
}

.footer__link:focus {
  outline: 2px solid #E7E61A;
  outline-offset: 2px;
}

@media (max-width: 992px) {
  .footer__link {
    text-align: center;
    margin-bottom: 15px;
  }
}

.thanks {
  padding: 100px 0 50px;
}

.thanks__container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.thanks__title {
  margin: 0;
  margin-bottom: 50px;
  max-width: 100%;
  text-align: center;
  font-size: calc(18px + 36 * ((100vw - 320px) / 1110));
  max-width: 100%;
  animation: 3s neon alternate-reverse infinite;
}

.thanks__link {
  max-width: 300px;
  font-size: calc(18px + 6 * ((100vw - 320px) / 1110));
}

.politics {
  padding-top: 100px;
