  line-height: 1.4;
  opacity: 0.85;
}

.politics__container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.politics__version {
  width: 100%;
  margin-bottom: 30px;
  font-style: italic;
}

.politics__title {
  max-width: 100%;
  color: #ffffff;
  font-size: calc(22px + 8 * ((100vw - 320px) / 1110));
}

.politics__subtitle {
  width: 100%;
  margin: 0;
  margin-bottom: 15px;
  font-size: calc(18px + 6 * ((100vw - 320px) / 1110));
}

.politics__descr {
  margin: 0;
  margin-bottom: 30px;
  font-size: calc(16px + 2 * ((100vw - 320px) / 1110));
}

.politics-list {
  padding-left: 50px;
  margin-bottom: 40px;
  font-size: calc(16px + 2 * ((100vw - 320px) / 1110));
}

@media (max-width: 720px) {
  .politics-list {
    padding-left: 20px;
  }
}

.politics-list__item {
  margin-bottom: 5px;
}

.politics__text {
  margin: 0;
  margin-bottom: 5px;
}

.politics__link {
  text-decoration: underline;
}

.not-found {
  padding: 100px 0 50px;
}

.not-found__wrap {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.not-found__title {
  margin: 0;
  margin-bottom: 30px;
  font-size: calc(50px + 40 * ((100vw - 320px) / 1110));
  text-transform: uppercase;
  color: #E7E61A;
  animation: 3s neon alternate-reverse infinite;
}

.not-found__subtitle {
  margin: 0;
  margin-bottom: 40px;
  font-size: calc(14px + 10 * ((100vw - 320px) / 1110));
  color: #ffffff;
}

.not-found__link {
  padding: 15px 20px;
  max-width: max-content;
  font-size: calc(20px + 2 * ((100vw - 320px) / 1110));
}

@media (max-width: 480px) {
  .not-found__link {
    max-width: 100%;
    width: 100%;
  }
}
