.whom {
	@media (max-width : 480px) {
		padding: 20px 0; }

	&__wrap {
		display: flex;
		align-items: flex-start;
		flex-direction: column; }

	&__text {
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-bottom: 40px;
		color: $white;
		animation: fadeIn 1.5s;
		@media	(max-width : 480px) {
			flex-direction: column; } }

	&__icon {
		margin-right: 100px;
		width: 75px;
		max-height: 80px;
		height: auto;
		fill: $broom;
		@media	(max-width : 768px) {
			margin-right: 40px;
			max-height: 50px; }
		@media	(max-width : 480px) {
			margin: 0 auto;
			margin-bottom: 15px; } }

	&__inner {
		position: relative;
		width: 100%;

		&::after {
			content: '';
			position: absolute;
			top: 50%;
			left: -55px;
			width: 10px;
			height: 10px;
			border-radius: 100%;
			background-color: $broom;
			transform: translateY(-50%);

			@media	(max-width : 768px) {
				display: none; } } }

	&__title {
		margin: 0;
		margin-bottom: 15px;
		@include adaptive-font(24, 22);
		text-align: start; }

	&__descr {
		margin: 0;
		@include adaptive-font(20, 18);
		opacity: .85;
		text-align: start; } }
