.section {
	padding: 80px 0;
	background-color: $accent-color;
	@media (max-width : 768px) {
		padding: 40px 0; } }

.logo {
	z-index: 5;
	display: block;
	&:focus {
		outline: 2px solid $broom;
		outline-offset: 2px; }
	&__img {
		max-width: 260px;
		height: 70px;
		vertical-align: bottom;
		fill: $broom;
		cursor: pointer;
		transition: all .2s;
		&--scroll {
			max-width: 230px; }
		@media (max-width: 992px) {
			max-width: 200px; } } }

.title {
	margin: 0;
	margin-bottom: 50px;
	padding-top: 20px;
	font-family: "Derold", sans-serif;
	text-transform: uppercase;
	letter-spacing: 2px;
	color: $broom;
	@include adaptive-font(40, 28);
	animation: fadeIn 1s;
	@media (max-width : 768px) {
		text-align: center; }
	@media (max-width : 480px) {
		margin-bottom: 25px; } }

.link {
	display: inline-block;
	padding: 15px 20px;
	text-align: center;
	border: 2px $broom solid;
	background-color: $broom;
	color: $black;
	transition: all .2s;
	border-radius: 15px;

	&:hover {
		border: 2px $broom solid;
		color: $white;
		background-color: transparent; }
	&:focus {
		outline: 2px solid $broom;
		outline-offset: 2px;
		background-color: transparent;
		color: $white; } }

.btn {
	margin-right: auto;
	padding: 20px 60px;
	width: 300px;
	border: 2px $broom solid;
	background-color: $accent-color;
	color: $white;
	transition: all .2s;
	border-radius: 5px;

	&:hover {
		border: 2px $broom solid;
		color: $accent-color;
		background-color: $broom; }

	&--form {
		width: 100%;
		padding: 15px 60px;
		@media (max-width : 768px) {
			width: 100%; } }

	&--scroll {
		padding: 10px 15px; } }

@keyframes neon {
	from {
		text-shadow: 0 0 20px $broom;
		text-shadow: 0 0 35px $broom;
		text-shadow: 0 0 40px $broom;
		text-shadow: 0 0 50px $broom;
		text-shadow: 0 0 800px $broom; }

	to {
		text-shadow: 0 0 5px $broom;
		text-shadow: 0 0 10px $broom;
		text-shadow: 0 0 15px $broom;
		text-shadow: 0 0 20px $broom; } }

@media (max-width: 768px) {
	.wow {
		-o-transition-property: none !important;
		-moz-transition-property: none !important;
		-ms-transition-property: none !important;
		-webkit-transition-property: none !important;
		transition-property: none !important {
			/*CSS transforms*/ }
		-o-transform: none !important;
		-moz-transform: none !important;
		-ms-transform: none !important;
		-webkit-transform: none !important;
		transform: none !important {
			/*CSS animations*/ }
		-webkit-animation: none !important;
		-moz-animation: none !important;
		-o-animation: none !important;
		-ms-animation: none !important;
		animation: none !important; } }
