.hero {
	min-height: 800px;
	padding-top: 100px;
	background-color: $accent-color;
	@media (max-width: 768px) {
		min-height: 700px; }

	&__container {
		display: flex;
		align-items: center;
		justify-content: center;
		height: 100%; }

	&__wrap {
		display: flex;
		align-items: center;
		justify-content: space-between;
		height: 100%;
		width: 100%;
		padding-top: 100px; }

	&__card {
		display: flex;
		flex-direction: column;
		max-width: 750px;
		color: $white;
		animation: fadeInLeft 1s;
		@media	(max-width : 768px) {
			align-items: center; } }

	&__text {
		max-width: 720px;
		margin-bottom: 50px;
		@media	(max-width : 768px) {
			margin-bottom: 50px; } }

	&__title {
		margin: 0;
		margin-bottom: 20px;
		font-family: "Derold", sans-serif;
		text-transform: uppercase;
		letter-spacing: 2px;
		line-height: 1;
		@include adaptive-font (70, 32);
		color: $broom;
		@media	(max-width : 768px) {
			text-align: center;
			margin-bottom: 20px; } }

	&__subtitle {
		margin: 0;
		@include adaptive-font (22, 20);
		@media	(max-width : 768px) {
			text-align: center; } }

	&__link {
		max-width: max-content;
		padding: 15px 20px;
		@include adaptive-font (22, 18);
		@media	(max-width : 480px) {
			max-width: 100%;
			width: 100%; } }

	&__img {
		animation: fadeInRight 1s;

		img {
			width: 400px;
			height: auto;
			@media	(max-width : 992px) {
				width: 350px; }
			@media	(max-width : 768px) {
				display: none; } } } }
